import _shell from 'shelljs'

export function shell(options = {}){
  options.async = true

  console.log('Shell', _shell)
  let proc = _shell(options.cmd, options)

  let { data, log } = options

  proc.stdout.on('data', function(buffer){
    data(
      buffer.toString()
    )
  })

  return proc
}



